*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.section {
  max-width: 1600px;
  text-align: center;
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 60px;
}

body {
  font-family: var(--fontPrimary), var(--fontPrimaryAlternative);
  background-color: var(--bgcPage);
  color: var(--colorBlack);
  letter-spacing: .04em;
  font-size: 14px;
  font-weight: 500;
}

.body-fixed {
  width: 100%;
  position: fixed;
}

h1, h2, h3, p, ul, li {
  margin: 0;
}

ul, li {
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

address {
  font-style: normal;
}

button {
  font: inherit;
  color: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

:focus-visible {
  outline: none;
}

:root {
  --colorPrimary: #d41443;
  --colorBlack: #000;
  --colorWhite: #fff;
  --colorLine: #e1e1e1;
  --colorOverline: #ffa5ba;
  --colorBorder: #e18298;
  --colorDescription: #907e82;
  --fontPrimary: "DM Sans";
  --fontPrimaryAlternative: sans-serif;
  --fontSecondary: "Titan One";
  --fontSecondaryAlternative: cursive;
  --bgcPage: #fff;
  --bgcSection: #fff5f6;
  --bgcHeader: #ffb8ca;
  --bgcMenuMobile: #e17992;
  --bgcBackDrop: rgba(255, 184, 202, .6);
  --bgcColorBeige: #f0d1a5;
  --bgcColorLightGreen: #c2e297;
  --bgcBtnDark: #d41443;
  --bgcBtnDarkHover: #c7133e;
  --bgcBtnWhite: #fff;
  --bgcBtnWhiteHover: #ecebeb;
  --bgcQuote: #fafafa;
  --bgcSocial: #fea5bb;
  --paddingContainer: 16px;
  --shadowMenuMobile: -8px 0px 20px rgba(0, 0, 0, .25);
  --menuMobileWidth: 250px;
  --menuMobileHeight: 502px;
  --menuTabletWidth: 254px;
  --menuTabletHeight: 100vh;
  --durationPrimary: 250ms;
  --durationSecondary: 400ms;
  --timingPrimary: cubic-bezier(.4, 0, .2, 1);
  --timingSecondary: ease-out;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
