  color: inherit;
  padding: 14px 0;
}

.header__menu-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
}

.header__menu-mobile {
  width: 20px;
  height: 12px;
  flex-shrink: 0;
